import { lazy, Suspense } from "react";
import { Switch, Route, useRouteMatch } from "react-router-dom";
import store from "@/MST/store";
import { SideMenu, Spinner } from "@/components";

import styles from "./Cabinet.module.scss";

const Billing = lazy(
  () => import("@/views/Cabinet/Billing" /* webpackChunkName: "Billing" */)
);
const Settings = lazy(
  () => import("@/views/Cabinet/Settings" /* webpackChunkName: "Settings" */)
);
const Stats = lazy(
  () => import("@/views/Cabinet/Stats" /* webpackChunkName: "Stats" */)
);

type Props = {};

const Cabinet: React.FC<Props> = () => {
  const { path, url } = useRouteMatch();
  const { isAuthenticated } = store.user;
  console.log(path, url);
  return (
    <div className={styles.cabinet}>
      Cabinet.
      {isAuthenticated ? <SideMenu /> : null}
      <Suspense fallback={<Spinner />}>
        <Switch>
          <Route path={`/cabinet/stats`} component={Stats} />
          <Route path={`/cabinet/billing`} component={Billing} />
          <Route path={`/cabinet/settings`} component={Settings} />
        </Switch>
      </Suspense>
    </div>
  );
};

export default Cabinet;
